import { validateEmpty } from "helpers/validate"

export const validateQuestion = (question) => {
    return [
        validateEmpty(question.text, 'الرجاء ادخال نص السؤال المراد اضافته التقييم'),
        validateEmpty(question.evaluateType, 'الرجاء اختيار نوع التقييم لاضافة السؤال للتقييم')
    ].filter(message => message) 
}