import { 
    validateArrayOfEmptyObjects, 
    validateDuplicateItemsInArray, 
    validateEmpty, 
    validateEmptyPropObjectInArray, 
    validateIsPositiveNumberPropObjectInArray, 
    validateIsNumberPropObjectsInArray 
} from '../../../helpers/validate';

export const validateExperiment = (experiment, itemsFieldName) => {
    return [
        validateEmpty(experiment.title, 'يجب ادخال اسم التجربة لاكمال عملية الاضافة'),
        validateEmpty(experiment.courseId, 'يجب اختيار المادة الدراسية الخاصة بالتجربة'),
        validateArrayOfEmptyObjects(experiment[itemsFieldName], { value: '', quantity: '' }, 'يجب اختيار مادة واحدة على الاقل لتنفيذ التجربة'),
        validateEmptyPropObjectInArray(experiment[itemsFieldName], 'quantity', 'يجب ادخال الكميات المرادة لتنفيذ التجربة لجميع المواد المختارة'),
        validateIsNumberPropObjectsInArray(experiment[itemsFieldName], 'quantity', 'يجب ان لا تحتوي الكمية على حروف او رموز'),
        validateIsPositiveNumberPropObjectInArray(experiment[itemsFieldName], 'quantity', 'لا يمكن أن تحتوي الكميات على قيم سالبة او فارغة أثناء عملية الانشاء'),
        validateDuplicateItemsInArray(experiment[itemsFieldName], 'value', 'لا يمكن اضافة عهد مكررة في نفس التجربة العملية')
    ].filter(message => message)
}
