// @ts-nocheck
import { connect } from 'react-redux';
import React, { Component } from 'react'
import _ from 'lodash';

import CustomDropdown from 'components/Form/CustomDropdown';
import { Form } from 'components/Form/Form';
import FormHeader from 'components/Form/FormHeader';
import InputField from 'components/Form/InputField';
import ContentDivider from 'components/Utils/ContentDivider';
import ContentPadding from 'components/Utils/ContentPadding';
import NotAllowed from 'components/Utils/NotAllowed';
import NotFoundItem from 'components/Utils/NotFoundItem';
import { getLabelObject } from 'helpers/objectsHelpers';
import CustomDatePicker from 'components/Form/CustomDatePicker';
import { createPracticalExperimentRequestAction, fetchCreateExperimentRequirementsAction } from 'actions/pagesActions/SchoolMember/ScienceTeacher/experimentsAction';
import { classesList } from 'helpers/fixedLists';
import { isNumberGreatThanZero, validateEmpty } from 'helpers/validate';
import Alert from 'components/Utils/Alert';
import parseDateToString from 'helpers/parseDate';

class PracticalExperimentCreate extends Component {

    constructor(props) {
        super(props);

        this.courseDropdownRef = React.createRef();
        this.experimentDropdownRef = React.createRef();
        this.labDropdownRef = React.createRef();
        this.classNoRef = React.createRef();

        this.state = {
            courseId: '',
            experimentId: '',
            itemsList: [],
            repeatExperiment: '',
            executeDate: null,
            labId: '',
            classNo: '',
            errorMessages: []
        }

        this.baseState = { ...this.state };
    }

    async componentDidMount() {
        if(this.props.hasSchool && (!this.props.courses.length || !this.props.labs.length)) {
            await this.props.fetchCreateExperimentRequirementsAction();
        }
    }

    updateCourseId = (value) => {
        // after choosing a course, reset other fields 
        this.setState({ ...this.baseState, courseId: value }, () => {
            if(this.experimentDropdownRef.current) {
                this.experimentDropdownRef.current.clearSelection();
            }
        })
    }

    updateExperimentId = (value) => {
        // after choosing a experiment, show items of this experiment with other fields
        const experiments = getLabelObject(this.state.courseId, this.props.courses) ? getLabelObject(this.state.courseId, this.props.courses).practicalExperiments : [];
        const itemsList = getLabelObject(Number(value), experiments) ? _.cloneDeep(getLabelObject(Number(value), experiments).items) : [];
        this.setState({ ...this.baseState, experimentId: value, itemsList, courseId: this.state.courseId }, () => {
            if(this.labDropdownRef.current && this.classNoRef.current) {
                this.labDropdownRef.current.clearSelection();
                this.classNoRef.current.clearSelection();
            }
        });
    }

    createExperiment = async () => {
        if(!this.state.errorMessages.length) {
            // after validating messages, make POST request
            await this.props.createPracticalExperimentRequestAction({
                ...this.state,
                executeDate: parseDateToString(this.state.executeDate),
                quantity: Number(this.state.repeatExperiment)
            })
            if(this.props.status) {
                // if status TRUE reset fields; else, let them update fields 
                this.onResetHandler();
            }
        }
    }

    onFieldChange = (value, fieldName) => {
        switch (fieldName) {
            case 'courseId':
                this.updateCourseId(value);
                break;
            case 'experimentId':
                this.updateExperimentId(value);
                break;
            default:
                // set all other fields that don't need extra operations
                this.setState({ [fieldName]: value });
                break;
        }
    }

    onResetHandler = () => {
        this.setState({ ...this.baseState });
        this.courseDropdownRef.current.clearSelection();
    }

    onFormSubmit = (e) => {
        e.preventDefault();
        this.setState({ 
            errorMessages: this.validateExperiment() 
        }, () => {
            this.createExperiment();
        })
    }

    validateExperiment = () => {
        if(!this.state.courseId) {
            return ['يجب اختيار المادة الدراسية التابعة للتجربة أولا'];
        } else if(!this.state.experimentId) {
            return ['يجب اختيار التجربة العلمية المراد تنفيذها']
        } else {
            return [
                isNumberGreatThanZero(this.state.repeatExperiment, 'يجب ادخال المرات المراد فيها تكرار التجربة للطلاب', 'مرات التكرار يجب أن تكون أرقام فقط, لا يسمح بادخال حروف أو رموز', 'لا يمكن أن تكون مرات تكرار التجربة أقل من مرة واحدة '),
                validateEmpty(this.state.executeDate, 'يجب اختيار التاريخ المراد تنفيذ التجربة بة'),
                validateEmpty(this.state.labId, 'يجب اختيار المختبر المراد تنفيذ التجربة بداخلة'),
                validateEmpty(this.state.classNo, 'الرجاء اختيار موعد الحصة الدراسية')
            ].filter(message => message)
        }
    }

    renderExperimentDropdown = () => {
        if(this.state.courseId) {
            
            let experiments = getLabelObject(this.state.courseId, this.props.courses) 
            // if the course have experiments return it; else, return [] 
            experiments = experiments ? experiments.practicalExperiments : [];
            
            return (
                <CustomDropdown 
                    label="التجربة العملية"
                    title="اختر التجربة العملية"
                    colSize="2"
                    list={experiments}
                    setRef={(ref) =>  this.experimentDropdownRef = ref}
                    onChange={(experimentId) => this.onFieldChange(experimentId, 'experimentId')}
                />
            );
        }
    }

    renderExperimentItemsField = () => {
        // render the items of experiment with other required fields, classNo, repeat, etc..
        if(this.state.itemsList.length) {
            const renderArray = this.state.itemsList.map((item, index) => {
                return (
                    <div className="form-group" key={item.value}>
                        <InputField
                            label="نوع العهدة"
                            locked={true}
                            icon="fas fa-lock"
                            initial={true}
                            initialValue={item.label}
                            colSize="2"
                        />
                        <InputField 
                            id={`field${index}`}
                            name={`quantity`}
                            label="الكمية"
                            icon="fas fa-lock"
                            initial={true}
                            initialValue={item.quantity}
                            locked={true}
                            colSize="2"
                        />
                    </div>
                );
            })

            renderArray.push(
                <div className="form-group" key="repeatExperiment">
                    <InputField 
                        id={`repeat`}
                        name={`repeat`}
                        label="مرات تنفيذ التجربة"
                        icon="fas fa-sort-numeric-up-alt"
                        initial={true}
                        initialValue={this.state.repeatExperiment}
                        onChangeHandler={(e) => this.onFieldChange(e.target.value, 'repeatExperiment')}
                        colSize="2"
                    />
                    <CustomDatePicker 
                        id="execute-date"
                        name="execute-date"
                        label="تاريخ تنفيذ التجربة"
                        initial={true}
                        initialDate={this.state.executeDate}
                        onChangeHandler={(executeDate) => this.onFieldChange(executeDate, 'executeDate')}
                        colSize="2"
                        minDate={new Date().setDate(new Date().getDate() + 2)}
                    />
                </div>
            );

            renderArray.push(
                <div className="form-group" key="labId">
                    <CustomDropdown 
                        label="المختبر"
                        title="اختر المختبر"
                        list={this.props.labs}
                        setRef={(ref) => this.labDropdownRef = ref }
                        onChange={(labId) => this.onFieldChange(labId, 'labId')}
                        colSize="2"
                    />
                    <CustomDropdown 
                        label="موعد الحصة"
                        title="اختر الحصة"
                        list={classesList}
                        setRef={(ref) => this.classNoRef = ref }
                        onChange={(classNo) => this.onFieldChange(classNo, 'classNo')}
                        colSize="2"
                    />
                </div>
            );
            return renderArray;
        }
    }

    render() {
        return (
            <NotAllowed
                hasSchool={this.props.hasSchool}
                referer="/invites"
                statusCode="403"
                message="انت غير مسجل في مدرسة لعرض هذة الصفحة"
                btnLabel="الذهاب لعرض طلبات الانضمام"
            >
                <NotFoundItem
                    referer="/"
                >
                    <Form onSubmit={this.onFormSubmit}>
                        <FormHeader 
                            label="طلب تجربة عملية جديدة"
                        />
                        <ContentPadding>
                            <Alert messages={this.state.errorMessages} type="404" />
                            <div className="form-group">
                                <CustomDropdown 
                                    label="المادة الدراسية"
                                    title="اختر المادة الدراسية للتجربة"
                                    list={this.props.courses}
                                    onChange={(courseId) => this.onFieldChange(courseId, 'courseId')}
                                    setRef={(ref) => this.courseDropdownRef = ref }
                                    colSize="2"
                                />
                                { this.renderExperimentDropdown() }
                            </div>
                            { this.renderExperimentItemsField() }
                            <ContentDivider />
                            <div className="form-group confirm-group margin-top-big">
                                <input className="btn submit-btn" type="submit" value="انشاء الطلب"/>
                                <input className="btn cancel-btn" type="reset" value="اعادة ضبط" onClick={this.onResetHandler}/>
                            </div>
                        </ContentPadding>
                    </Form>
                </NotFoundItem>
            </NotAllowed>
        )
    }
}

const mapStateToProps = state => {
    return {
        hasSchool: state.auth.user.data.hasSchool,
        courses: Object.values(state.courses),
        labs: Object.values(state.laboratories),
        status: state.alert.success
    }
}

export default connect(mapStateToProps, { 
    fetchCreateExperimentRequirementsAction,
    createPracticalExperimentRequestAction
})(PracticalExperimentCreate);