// @ts-nocheck
import ContentPadding from 'components/Utils/ContentPadding'
import TextContainer from 'components/Text/TextContainer'
import React, { Fragment } from 'react'
import { Link } from 'react-router-dom'
import BulletContainer from 'components/Text/BulletContainer'
import BulletPoint from 'components/Text/BulletPoint'
import TitleHeader from 'components/Text/TitleHeader'
import ParagraphBlock from 'components/Text/ParagraphBlock'
import { connect } from 'react-redux'

const SchoolManagerHomePage = ({ hasSchool }) => {


    const renderInstructions = () => {

        if(hasSchool) {
            return (
                <Fragment>
                    <BulletContainer
                        title=":لاضافة اعضاء في المدرسة اتبع الخطوات التالية"
                    >
                        <BulletPoint>
                            &nbsp; اختر من القائمة الطولية  
                            <Link to="/members/create" className="color-primary"> عضو في المدرسة </Link>           
                        </BulletPoint>
                        <BulletPoint>
                            .لاضافة عضو, عليك ارسال دعوة الانضمام
                        </BulletPoint>
                        <BulletPoint>
                            قم بادخال رقم حساب العضو المراد دعوتة ومن ثم اضغط زر الارسال
                        </BulletPoint>
                    </BulletContainer>
                    <BulletContainer
                        title=":لاضافة مختبر مدرسي اتبع الخطوات التالية"
                    >
                        <BulletPoint>
                            &nbsp; اختر من القائمة الطولية  
                            <Link to="/laboratory/create" className="color-primary"> مختبر </Link>
                        </BulletPoint>
                        <BulletPoint>
                            .قم بادخال رقم المختبر المدرسي
                        </BulletPoint>
                        <BulletPoint>
                            يمكنك اختيار محضرين المختبر من القائمة المنسدلة
                        </BulletPoint>
                        <BulletPoint>
                            بعد ذلك اضغط زر الاضافة
                        </BulletPoint>
                    </BulletContainer>
                    <BulletContainer
                        title=":يمكنك القاء نظرة عامة على المدرسة ,المختبرات ومحضريها"
                    >
                        <BulletPoint>
                            &nbsp; اختر من القائمة الطولية  
                            <Link to="/school" className="color-primary"> المدرسة </Link>
                        </BulletPoint>
                        <BulletPoint>
                            &nbsp; اختر من القائمة الطولية  
                            <Link to="/dashboard/laboratory-manager" className="color-primary"> محضرين المختبر </Link>
                        </BulletPoint>
                        <BulletPoint>
                            &nbsp; اختر من القائمة الطولية  
                            <Link to="/dashboard/laboratory" className="color-primary">  المختبرات </Link>
                        </BulletPoint>
                    </BulletContainer>
                </Fragment>
            );
        }

        return (
            <Fragment>
                <BulletContainer
                    title=":لانشاء مدرسة في النظام اتبع الخطوات التالية"
                >
                    <BulletPoint>
                        &nbsp; اختر من القائمة الطولية  
                        <Link to="/school/create" className="color-primary"> مدرسة جديدة </Link>           
                    </BulletPoint>
                    <BulletPoint>
                        .في نموذج انشاء مدرسة جديدة, قم بادخال معلومات المدرسة الخاصة بك
                    </BulletPoint>
                    <BulletPoint>
                        بعد ادخال البيانات, اضغط زر الانشاء للمتابعة وانشاء المدرسة
                    </BulletPoint>
                </BulletContainer>
            </Fragment>
        );
    }

    return (
        <TextContainer>
            <TitleHeader title="عن النظام" />
            <ParagraphBlock>
                منصة مختبراتي هي منصة تجريبية الكترونية تهدف أن تكون تابعة لوزارة التربية والتعليم في المملكة 
                العربية السعودية, وهي منصَّة الكترونية تساعد محضِّري المختبرات المدرسية على إدارة المُختَبر وجميع
                فعالياتة, بحيث يستطيع محضِّر المختبر القيام بجميع المهام المتعلقة بإدارة المختبر كتسجيل العهد
                الصادرة والواردة أو القيام بعمليات الجرد. وتسمح المنصَّة لِمعلم العلوم بطلب تجهيز التجارب
                العلمية من محضر المختبر. وتستطيع المدارس استعارة العهد أو الأدوات من بعضها البعض
                من خلال نظام الطلبات الإلكترونية التي سوف توفرها المنصَّة. وأيضا تقوم المنصَّة بتوفير نظام التقيِّيم 
                الالكتروني لمقيِّمين المختبر, ومن مهام منصَّة مُختَبراتِي إنشاء التقارير لقائد المدرسة
                والمشرف التربوي الكترونيًّا.
            </ParagraphBlock>

            <TitleHeader title="التعليمات" />
            <ContentPadding>
                <ContentPadding>
                    { renderInstructions() }
                </ContentPadding> 
            </ContentPadding> 
        </TextContainer>
    )
}

const mapStateToProps = (state) => {
    return {
        hasSchool: state.auth.user.data.hasSchool,
    }
}

export default connect(mapStateToProps)(SchoolManagerHomePage)
