// @ts-nocheck
import React, { Component } from 'react'
import Chart from "react-google-charts";
import { connect } from 'react-redux';

import { clearDashboardAction, fetchSchoolLaboratoryManagersDashboardAction } from 'actions/pagesActions/SchoolManager/dashboardAction';
import Card from 'components/Dashboard/Card';
import CardContainer from 'components/Dashboard/CardContainer';
import ChartContainer from 'components/Dashboard/ChartContainer';
import List from 'components/Dashboard/List';
import LoadingIndicator from 'components/Dashboard/LoadingIndicator';
import OverviewMetric from 'components/Dashboard/OverviewMetric';
import ContentPadding from 'components/Utils/ContentPadding';
import NotAllowed from 'components/Utils/NotAllowed';
import { isSameProps } from 'helpers/objectsHelpers';
import { validateEqualPropsObjects } from 'helpers/validate';

const EXPERIMENTS_COLUMNS_HEADERS = [
    {
        label: 'مرات التنفيذ',
        key: 'qunatity'
    },
    {
        label: 'اسم التجربة',
        key: 'experimentExecuted'
    }
];

const LAB_MANAGERS_RATES = [
    {
        label: 'التقييم',
        key: 'rate'
    },
    {
        label: 'اسم المحضر',
        key: 'name'
    },
]

class LabManagerDashboard extends Component {

    state = {
        fromDate: '',
        toDate: ''
    }

    componentDidMount () {
        // DASHBOARD API CALL HERE
        this.props.fetchSchoolLaboratoryManagersDashboardAction()
    }

    render() {
        return (
            <NotAllowed
                hasSchool={this.props.hasSchool}
                referer="/school/create"
                statusCode="403"
                message="لا يوجد لديك مدرسة لعرضها, الرجاء انشاء واحدة"
                btnLabel="الذهاب لانشاء مدرسة"
            >
                <ContentPadding>
                    <div className="dashboard">
                        { /* <DateFilter /> */ }
                        <div className="dashboard-flex">
                            <div className="dashboard-flex__50">
                                <CardContainer>
                                    <Card
                                        title="محضرين المختبر"
                                    >
                                        <span> { this.props.dashboard.labManagersCount } </span> 
                                    </Card>
                                    <Card
                                        title="المختبرات"
                                    >
                                        <span > { this.props.dashboard.numberLabs } </span>
                                    </Card>
                                </CardContainer>
                                <ChartContainer 
                                    title="ملخص تفاعل المحضرين"
                                    className="dashboard-chart-area dashboard-chart-area-row-span-list"
                                >
                                    <Chart
                                        width={'670px'}
                                        height={'100%'}
                                        chartType="ColumnChart"
                                        loader={<div>Loading Chart</div>}
                                        data={[
                                            ['المحضرين', 'المقبولة', 'المرفوضة'],
                                            ...this.props.dashboard.labManagersExperimentsSummary,
                                        ]}
                                        options={{
                                            // Material design options
                                            chartArea: { width: '70%', height: '70%' },
                                            colors: ['#0FA591' ,'#C0392B'],
                                            bar: { groupWidth: "30%" }
                                        }}
                                        // For tests
                                        rootProps={{ 'data-testid': '2' }}
                                    />
                                </ChartContainer>
                                <List 
                                    title='محضرين المختبرات'
                                    colHeaders={LAB_MANAGERS_RATES}
                                    data={this.props.dashboard.rates}
                                />
                            </div>
                            <div className="dashboard-flex__50">
                                <OverviewMetric 
                                    title="التجارب"
                                    metricsData={[
                                        { label: 'الكل', value: this.props.dashboard.totalExperiments },
                                        { label: 'المقبولة', value: this.props.dashboard.approvedExperiments, color: 'color-primary' },
                                        { label: 'المرفوضة', value: this.props.dashboard.notApprovedExperiments, color: 'color-danger' },
                                    ]}
                                />
                                <ChartContainer 
                                    title="ادارة العهد"
                                    className="dashboard-chart-area dashboard-chart-area-row-span-list"
                                >
                                    <Chart
                                        width={'720px'}
                                        height={'100%'}
                                        chartType="AreaChart"
                                        loader={<LoadingIndicator />}
                                        data={[
                                            ['السنة', 'الاضافة', 'الحذف'],
                                            ...this.props.dashboard.itemActivity
                                        ]}
                                        options={{
                                            // For the legend to fit, we make the chart area smaller
                                            chartArea: { width: '70%', height: '70%' },
                                            // lineWidth: 25
                                            colors: ['#0FA591' ,'#C0392B'],
                                            bar: { groupWidth: '10%' },
                                        }}
                                        // For tests
                                        rootProps={{ 'data-testid': '1' }}
                                    />
                                </ChartContainer>

                                <List 
                                    title='التجارب المنفذة'
                                    colHeaders={EXPERIMENTS_COLUMNS_HEADERS}
                                    data={this.props.dashboard.numberExecuteExperiment}
                                />
                                <span>&nbsp;</span>
                            </div>
                        </div>
                    </div>
                </ContentPadding>
            </ NotAllowed>
        )
    }

    componentWillUnmount() {
        this.props.clearDashboardAction();
    }
}

const mapStateToProps = state => {
    let dashboard = validateEqualPropsObjects(state.dashboard, {}) ? undefined : state.dashboard;

    if(dashboard && !isSameProps(dashboard, LabManagerDashboard.defaultProps.dashboard)) {
        dashboard = undefined;
    }

    return {
        dashboard: dashboard,
        hasSchool: state.auth.user.data.hasSchool,
    }
}

LabManagerDashboard.defaultProps = {
    dashboard: {
        validItem: 0,
        willExpireItem: 0,
        expiredItem: 0,
        approvedExperiments: 0,
        endSemester: "",
        itemActivity: [["", 0, 0]],
        notApprovedExperiments: 0,
        numberExecuteExperiment: [],
        numberExecutedDate: [],
        numberLabs: 0,
        rates: [],
        labManagersExperimentsSummary: [["", 0, 0]],
        startSemester: "",
        totalExperiments: 0,
        labManagersCount: 0
    }
}

export default connect(mapStateToProps, {
    fetchSchoolLaboratoryManagersDashboardAction,
    clearDashboardAction
})(LabManagerDashboard);