// @ts-nocheck
import React, { Component } from 'react';
import { connect } from 'react-redux';

import CheckBox from '../../../../components/Form/CheckBox';
import CustomDatePicker from '../../../../components/Form/CustomDatePicker';
import CustomDropdown from '../../../../components/Form/CustomDropdown';
import { Form } from '../../../../components/Form/Form';
import FormHeader from '../../../../components/Form/FormHeader';
import InputField from '../../../../components/Form/InputField';
import Alert from '../../../../components/Utils/Alert';
import ContentDivider from '../../../../components/Utils/ContentDivider';
import ContentPadding from '../../../../components/Utils/ContentPadding';
import NotAllowed from '../../../../components/Utils/NotAllowed';

import { isNumberGreatThanZero, validateEmpty } from '../../../../helpers/validate';
import { fetchLaboratoryAction } from '../../../../actions/pagesActions/SchoolMember/LaboratoryManager/LaboratoriesAction';
import NotFoundItem from '../../../../components/Utils/NotFoundItem';
import { createLabItemAction } from '../../../../actions/pagesActions/SchoolMember/LaboratoryManager/LaboratoryItemsAction';

class LabInfo extends Component {

    constructor(props) {
        super(props);

        this.state = {
            itemId: '',
            quantity: '',
            hasExpireDate: true,
            expireDate: null,
            errorMessages: []
        }
        this.baseState = { ...this.state };
    }

    async componentDidMount() {
        this.dropdownRef = React.createRef();
        // action call for the available invites
        if(this.props.hasSchool && (!this.props.laboratory || !this.props.ministryItems.length)) {
            // action to load lab information
            await this.props.fetchLaboratoryAction(this.props.match.params.labId);
        }
    }

    validateItem = () => {
        return [
            validateEmpty(this.state.itemId, 'يجب اختيار نوع العهدة لاضافتها'),
            isNumberGreatThanZero(this.state.quantity, 
                'يجب ادخال الكمية المتوفرة من العهدة الجديدة', 
                'الكمية يجب أن تكون أرقام فقط, لا يسمح بادخال حروف أو رموز', 
                'لا يمكن أن تكون كمية العهدة الجديدة فارغة أو بكمية سالبة'),
            this.state.hasExpireDate ? validateEmpty(this.state.expireDate, 'يجب ادخال تاريخ انتهاء الصلاحية') : ''
        ].filter(message => message)
    }

    addItem = async () => {
        if(!this.state.errorMessages.length) {
            // call the create action call here
            await this.props.createLabItemAction({
                itemId: this.state.itemId,
                hasExpireDate: this.state.hasExpireDate,
                expireDate: this.state.expireDate,
                quantity: Number(this.state.quantity)
            }, this.props.match.params.labId)

            if(this.props.status) {
                // reset fields upon success creation
                this.onResetHandler();
            }
        }
    }

    onFieldChange = (value, fieldName) => {
        this.setState({ [fieldName]: value });
    }

    onFormSubmit = (e) => {
        e.preventDefault();

        // validate item
        this.setState({ 
            errorMessages: this.validateItem()
        }, () => {
            this.addItem()
        })
    }

    onResetHandler = () => {
        this.setState({ ...this.baseState })
        this.dropdownRef.current.clearSelection();
    }

    renderLabManagers = () => {
        // render the laboratory managers of the current lab
        const { laboratory } = this.props;
        if(laboratory) {
            return laboratory.laboratoryManagers.map((labManager, index) => {
                return (
                    <div className="form-group" key={labManager.value}>
                        <InputField 
                            id={`labManager${labManager.value}`}
                            name={`labManager${labManager.value}`}
                            type="text"
                            label={`محضر المختبر: ${index+1}`}
                            locked={true}
                            initial={true}
                            initialValue={labManager.label}
                            icon="fas fa-lock"
                            colSize="1"
                        />
                    </div>
                );
            })
        }
    }

    render() {
        return (
            <NotAllowed
                hasSchool={this.props.hasSchool}
                referer="/invites"
                statusCode="403"
                message="انت غير مسجل في مدرسة لعرض هذة الصفحة"
                btnLabel="الذهاب لعرض طلبات الانضمام"
            >
                <NotFoundItem
                    referer="/school-laboratory"
                >
                    <Form onSubmit={ this.onFormSubmit } >
                        <FormHeader 
                            label={`محضرين المختبر`}
                            referer="/school-laboratory"
                            refererLabel="العودة للمختبرات"
                            icon="fas fa-chevron-left" 
                        />
                        <ContentPadding>
                            { this.renderLabManagers() }
                        </ContentPadding>
                        <FormHeader label="اضافة عهدة" />
                        <ContentPadding>
                            <Alert 
                                messages={this.state.errorMessages}
                                type="error"
                            />
                            <div className="form-group">
                                <CustomDropdown 
                                    list={this.props.ministryItems}
                                    onChange={(itemId) => this.onFieldChange(itemId, 'itemId')}
                                    title="اختر نوع العهدة"
                                    colSize="2"
                                    label="نوع العهدة"
                                    setRef={(ref) => this.dropdownRef = ref}
                                />
                                <InputField 
                                    id="quantity"
                                    name="quantity"
                                    type="text"
                                    onChangeHandler={(e) => this.onFieldChange(e.target.value, 'quantity')}
                                    label="الكمية"
                                    initial={true}
                                    initialValue={this.state.quantity}
                                    colSize="2"
                                    icon="fas fa-sort-numeric-up-alt"
                                />
                            </div>
                            <div className="form-group">
                                <CustomDatePicker 
                                    label="تاريخ انتهاء الصلاحية"
                                    colSize="2"
                                    locked={!this.state.hasExpireDate}
                                    initial={true}
                                    initialDate={this.state.expireDate}
                                    onChangeHandler={(expireDate) => this.onFieldChange(expireDate, 'expireDate')}
                                    weekend={false}
                                    minDate=
                                    { new Date().setDate(new Date().getDate() + 1) }
                                />
                                <div className="form-group__2-col">
                                <CheckBox 
                                    id="withEndDate"
                                    name="withEndDate"
                                    initial={true}
                                    initialValue={!this.state.hasExpireDate}
                                    label="لا يوجد تاريخ انتهاء"
                                    onChange={(checked) => this.onFieldChange(!checked, 'hasExpireDate')}
                                />
                                </div>
                            </div>
                            <ContentDivider />
                            <div className="form-group confirm-group margin-top-big">
                                <input className="btn submit-btn" type="submit" value="اضافة"/>
                                <input className="btn cancel-btn" type="reset" value="اعادة ضبط" onClick={this.onResetHandler}/>
                            </div>
                        </ContentPadding>
                    </Form>
                </NotFoundItem>
            </NotAllowed>
        );
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        hasSchool: state.auth.user.data.hasSchool,
        laboratory: state.laboratories[ownProps.match.params.labId],
        ministryItems: Object.values(state.items),
        status: state.alert.success
    }
}

export default connect(mapStateToProps, { 
    fetchLaboratoryAction,
    createLabItemAction
})(LabInfo);
