// @ts-nocheck
import React, { Fragment } from 'react'
import { Link } from 'react-router-dom'

import BulletContainer from 'components/Text/BulletContainer'
import BulletPoint from 'components/Text/BulletPoint'

const LabManagerHomePage = () => {

    return (
        <Fragment>
            <BulletContainer
                title=":لعرض المختبرات اتبع الخطوات التالية"
            >
                <BulletPoint>
                    &nbsp; اختر من القائمة الطولية  
                    <Link to="/school-laboratory" className="color-primary"> المختبرات </Link>           
                </BulletPoint>
                <BulletPoint>
                    القائمة الظاهرة هي المختبرات المسئؤول عنها في المدرسة
                </BulletPoint>
                <BulletPoint>
                    في حالة لا يوجد أي مختبر لادارتة, تواصل مع قائد المدرسة
                </BulletPoint>
            </BulletContainer>
            <BulletContainer
                title=":لاستعراض طلبات التجارب العملية اتبع الخطوات التالية"
            >
                <BulletPoint>
                    &nbsp; اختر من القائمة الطولية  
                    <Link to="/practical-request" className="color-primary"> الطلبات </Link>           
                </BulletPoint>
                <BulletPoint>
                    لاستعراض بطريقة سريعة, استخدم تصفية النتائج
                </BulletPoint>
            </BulletContainer>
        </Fragment>
    )
}

export default LabManagerHomePage;
