// @ts-nocheck
import _ from 'lodash';

export const isUpperCaseCharacterAletter = (char) => {
    // using Regex Experssion to identify if it's capital letter
    return (/[A-Z]/).test(char);
}

export const isCharacterAdigit = (char) => {
    // using Regex Experssion to identify if it's digit
    return (/[0-9]/).test(char);
}

export const validateId = (id) => {
    const arr = [];
    if(id.length !== 10) {
        arr.push('رقم الهوية يجب أن يكون 10 أرقام *');
    } 

    let containLetter = false;
    for (let c of id) {
        if(!isCharacterAdigit(c)) {
            containLetter = true;
            break;
        }
    }
    if(containLetter) {
        arr.push('رقم الهوية يجب أن يحتوي على أرقام فقط *');
    }
    return arr;
}

export const validatePassword = (password) => {
    if(password.length < 8) {
        return 'كلمة المرور يجب أن لا تقل عن 8 مدخلات *'
    } 

    let containCapital = false;
    for (let c of password) {
        if(isUpperCaseCharacterAletter(c)) {
            containCapital = true;
            break;
        }
    }
    if(!containCapital) {
        return 'كلمة المرور يجب أن تحتوي على حرف واحد كابيتال على الأقل *'
    }
    return '';
}

export const validateSaudiPhoneNumber = (phone) => {
    var regex = new RegExp(/^(009665|9665|\+9665|05|5)(5|0|3|6|4|9|1|8|7)([0-9]{7})$/);
    if(!regex.test(phone)) {
        return 'الرجاء ادخال رقم جوال بصيغة صحيحة *';
    }

    return '';
}

export const validateRoleType = (roleType) => {
    if(!roleType) {
        return 'يجب اختيار نوع الحساب لاتمام التسجيل *';
    }

    return '';
}

export const validateMatchedPasswords = (firstPass, secondPass) => {
    if(firstPass !== secondPass) {
        return 'كلمات المرور المدخلة غير متطابقة *';
    }

    return '';
}


export const validateName = (names) => {
    let message = 'الرجاء ادخال';
    names.map((name, index) => {
        if(index === 0 && !name) {
            message += ' الاسم الأول و';
        } else if(index === 1 && !name) {
            message += ' الاسم الأوسط و';
        } else if(index === 2 && !name) {
            message += ' الاسم الاخير و';
        }
        return '';
    });

    return message.length === 12 ? '' : message.slice(0, -1) + ' *';

}

export const validateEmail = (email) => {
    var regex = new RegExp(/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/);
    
    if(!regex.test(email)) {
        return 'الرجاء ادخال البريد الالكتروني بصيغة صحيحة *';
    }
    return '';
}

export const validateMatchedEmail = (email, confirmedEmail) => {
    if((!confirmedEmail && email) || (email !== confirmedEmail)) {
        return 'حقول البريد الالكتروني غير متطابقة *'
    }
    return '';
}

export const validateUpdatePassword = (oldPassword, newPassword, confirmedNewPassword) => {
    // Check if the letter is capital
    isUpperCaseCharacterAletter();
    const arr = [];
    if((newPassword || confirmedNewPassword) && !oldPassword) {
        arr.push('لتحديث كلمة المرور الرجاء ادخال كلمة المرور القديمة *');
    } else if((newPassword || confirmedNewPassword) && oldPassword){
        // User enter some password fileds
        if(oldPassword.length < 8) {
            arr.push('كلمة المرور القديمة يجب أن لا تقل عن 8 مدخلات *')
        } if(!isContainCapital(oldPassword)) {
            arr.push('كلمة المرور القديمة يجب ان تحتوي على حرف واحد كابيتال على الأقل *');
        } if(newPassword.length < 8 || confirmedNewPassword.length < 8) {
            arr.push('كلمة المرور الجديدة يجب أن لا تقل عن 8 مدخلات *');
        } if(!isContainCapital(newPassword) || !isContainCapital(confirmedNewPassword)) {
            arr.push('كلمة المرور الجديدة يجب أن تحتوي على حرف واحد كابيتال على الأقل *');
        } if(newPassword !== confirmedNewPassword) {
            arr.push('كلمات المرور الجديدة غير متطابقة *');
        }
    }

    return arr;
}

const isContainCapital = (password) => {
    // validate if the password have at least one capital letter
    let containCapital = false;
    for (let c of password) {
        if(isUpperCaseCharacterAletter(c)) {
            containCapital = true;
            break;
        }
    }

    return containCapital;
}

export const validateDates = (startDate, endDate) => {
    const arr = [];
    if (!startDate || !endDate) {
        arr.push('يجب اختيار تاريخ الابتداء والانتهاء');
        return arr;
    }
    if(startDate > endDate) {
        arr.push('التواريخ متعارضة!, لا يمكن لتاريخ الابتداء أن يكون بعد تاريخ الانتهاء *');
    } if(startDate.getDay() === 5 || startDate.getDay() === 6) {
        arr.push('تاريخ الابتداء لا يمكن ان يوافق اجازة نهاية الأسبوع *');
    } if(endDate.getDay() === 5 || endDate.getDay() === 6) {
        arr.push('تاريخ الانتهاء لا يمكن ان يوافق اجازة نهاية الأسبوع *');
    } if(startDate === endDate) {
        arr.push('تاريخ الابتداء مطابق لتاريخ الانتهاء *');
    }
    return arr;
}

export const validateTitle = (title) => {
    if(!title) {
        return 'يجب تعبئة عنوان الفصل الدراسي';
    }
}

export const validateEmpty = (value, emptyMessage) => {
    return !value ? emptyMessage : '';
}

export const validateEqualPropsObjects = (object1, object2) => {
    return _.isEqual(object1, object2);
}

export const validateArrayOfEmptyObjects = (array, emptyObject, message) => {
    // get array of empty objects in the original array
    const filteredObjects = array.filter(obj => validateEqualPropsObjects(obj, emptyObject))
    // if equal length, mean original array have empty objects
    return filteredObjects.length === array.length ? message : '';
}

export const validateEmptyPropObjectInArray = (array, emptyProp, message) => {
    // get array of empty selected prop objects in the original array
    const filteredObjects = array.filter(obj => !obj[emptyProp] && obj['value']);
    // if have at least one satisfied obj, return messafe
    return filteredObjects.length ? message : '';
}

export const isNumber = (input, emptyMessage, shouldBeNumberMessage) => {
    if(isNaN(input)) {
        return shouldBeNumberMessage;
    }
    if(input === '') {
        return emptyMessage;
    }
}

export const isWholeNumber = (input, emptyMessage, shouldBeNumberMessage, shouldBePositiveNumberMessage) => {
    let isNumberMessage = isNumber(input, emptyMessage, shouldBeNumberMessage);

    if(isNumberMessage) {
        return isNumberMessage;
    }
    if(Number(input) < 0) {
        return shouldBePositiveNumberMessage;
    }
}

export const isNumberGreatThanZero = (input, emptyMessage, shouldBeNumberMessage, zeroMessage) => {
    let isNumberMessage = isNumber(input, emptyMessage, shouldBeNumberMessage);

    if(isNumberMessage) {
        return isNumberMessage;
    }
    if(Number(input) <= 0) {
        return zeroMessage;
    }
}

export const validateIsNumberPropObjectsInArray = (array, filedName, message) => {
    // return filtered objects, if only they contain letters or special chars 
    const filteredObjects = array.filter(obj => isNaN(obj[filedName]));
    return filteredObjects.length ? message : '';
}

export const validateIsPositiveNumberPropObjectInArray = (array, objectProp, message) => {
    // return filtered objects, if only they contain values less than zero
    const filteredObjects = array.filter(obj => {
        return obj[objectProp] !== '' && isNumberGreatThanZero(obj[objectProp], '', '', message)
    });
    return filteredObjects.length ? message : '';
}

export const validateDuplicateItemsInArray = (array, key, duplicateMessage) => {
    const objectskeys = [];
    for(const obj of array) {
        if(objectskeys.includes(obj[key])) {
            return duplicateMessage;
        }
        objectskeys.push(obj[key])
    }
}