// @ts-nocheck
import ContentPadding from 'components/Utils/ContentPadding'
import TextContainer from 'components/Text/TextContainer'
import React from 'react'
import { Link } from 'react-router-dom'
import BulletContainer from 'components/Text/BulletContainer'
import BulletPoint from 'components/Text/BulletPoint'
import TitleHeader from 'components/Text/TitleHeader'
import ParagraphBlock from 'components/Text/ParagraphBlock'

const EducationalSupervisorHomePage = () => {
    return (
        <TextContainer>

            <TitleHeader title="عن النظام" />
            <ParagraphBlock>
                منصة مختبراتي هي منصة تجريبية الكترونية تهدف أن تكون تابعة لوزارة التربية والتعليم في المملكة 
                العربية السعودية, وهي منصَّة الكترونية تساعد محضِّري المختبرات المدرسية على إدارة المُختَبر وجميع
                فعالياتة, بحيث يستطيع محضِّر المختبر القيام بجميع المهام المتعلقة بإدارة المختبر كتسجيل العهد
                الصادرة والواردة أو القيام بعمليات الجرد. وتسمح المنصَّة لِمعلم العلوم بطلب تجهيز التجارب
                العلمية من محضر المختبر. وتستطيع المدارس استعارة العهد أو الأدوات من بعضها البعض
                من خلال نظام الطلبات الإلكترونية التي سوف توفرها المنصَّة. وأيضا تقوم المنصَّة بتوفير نظام التقيِّيم 
                الالكتروني لمقيِّمين المختبر, ومن مهام منصَّة مُختَبراتِي إنشاء التقارير لقائد المدرسة
                والمشرف التربوي الكترونيًّا.
            </ParagraphBlock>

            <TitleHeader title="التعليمات" />
            <ContentPadding>
                <ContentPadding>
                    <BulletContainer
                        title=":لانشاء زيارة يمكنك اتباع الخطوات التالية"
                    >
                        <BulletPoint>
                            .يجب التأكد من عدم وجود زيارة فعالة
                        </BulletPoint>
                        <BulletPoint>
                            &nbsp; اختر من القائمة الطولية  
                            <Link to="/visit/create" className="color-primary"> انشاء زيارة </Link>           
                        </BulletPoint>
                        <BulletPoint>
                            .في نموذج انشاء زيارة جديدة, قم بادخال رمز المدرسة المراد متابعتها
                        </BulletPoint>
                        <BulletPoint>
                            بعد ذلك قم بانشاء الزيارة, الان تستطيع أن تبدأ التقييم بسهولة
                        </BulletPoint>
                    </BulletContainer>
                    <BulletContainer
                        title=":لاغلاق الزيارة يمكنك اتباع الخطوات التالية"
                    >
                        <BulletPoint>
                            &nbsp; من خلال زيارة صفحة  
                            <Link to="/visit/current" className="color-primary"> الزيارة الحالية </Link>  
                        </BulletPoint>
                        <BulletPoint>
                            &nbsp;قم بالضغط على زر 
                            <span className="color-danger"> اغلاق الزيارة </span>
                        </BulletPoint>
                    </BulletContainer>
                </ContentPadding> 
            </ContentPadding> 
        </TextContainer>
    )
}

export default EducationalSupervisorHomePage
